section{
    background-color: #ffffff;
}

.comment_pane{
    width: 100%;
    margin-bottom: 20px;
}

.comment_input{
    padding: 7px;
}

.player img{
    width: 20px;
}

.chatcomment{
    height: 30px;
    background: #fff;
    border: 1px solid #428bca;
    border-radius: 10px;
    padding: 2px;
}

.play-container{
    width: 80%;
    margin: 0 auto;
    padding: 2%;
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: 490px){
    .play-container{
        width: 94%;
    }
}

.play-instructions{
    flex: 1;
}

.play-comment{
    flex: 1;
}

.play-chat{
    flex: 1;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.messages{
    height: 25%;
    flex: 2;
    display: flex;
    flex-direction: column;
    background-color: #eeeeee;
}

.message{
    flex: 1;
    background-color: #ffffff;
    box-shadow: 0 1px 2px 0 #cccccc, 0 1px 2px 0 #cccccc;
    padding: 4px;
    margin: 4px;
}

.players{
    flex: 1;
    display: flex;
    flex-direction: column;
}

.player{
    flex-basis: content;
    box-shadow: 0 1px 2px 0 #cccccc, 0 1px 2px 0 #cccccc;
    border-radius: 4px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.name{    
    flex: 2;
    color: #000000;
}

.vote{
    flex: 1;
    color: green;
}

.next-action{
    padding: 6px;
    border: #428bca;
}

.play-instructions{
    padding: 4%;
}

.play-instructions button{
    margin: 1%;
}

.play-instructions button:hover a{
    text-decoration: none;
    color: #ffffff;
}

.play-instructions a{
    text-decoration: none;
}
