.header{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    padding-top: 0px;
    margin-top: -3%;
}

.logo{
    flex: 2;
    color: #ffffff;
    font-weight: bolder;
    font-size: 1.7em;
    padding-left: 1%;
}

.logo img{
    width: 54%;
    padding-left: 10%;
}

@media screen and (max-width: 720px){ 
    .logo img{
        width: 75%;
    }
}

@media screen and (max-width: 490px){ 
    .logo img{
        width: 95%;
    }
}

.header-middle{
    flex: 6;
    text-align: right;
    padding-right: 4%;
    color: #ffffff;
}


.header-right{
    flex: 1;
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: 490px){
    .header-right{
        flex: 2.5;
    }
}

.sign-up{
    flex: 1;
    background: #ffffff;
    padding: 4%;
}

.sign-up a{
    color: #000000;
    text-decoration: none;
}

.sign-out{
    flex: 1;
    background: #ffffff;
    padding: 4%;
    cursor: pointer;
}

.sign-out a{
    color: #000000;
    text-decoration: none;
}

.sign-in{
    flex: 1;
    padding: 4%;
}

.sign-in a{
    color: #ffffff;
    text-decoration: none;
}

.name{
    flex: 1;
    padding: 4%;
    color: #ffffff;
}

.name img{
    width: 15%;
}

.footer{
    background: #ffffff;
    bottom: 0;
    left: 0;
    position: static;
    width: 100%;
    padding: 1%;
}
