html,
body {
  height: 100%;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #f5f5f5;
}

.auth-container{
  width: 100%;
  padding-top: 12%;
  padding-bottom: 4%;
}

@media screen and (max-width: 490px){
  .auth-container{
    padding-top: 20%;
    padding-bottom: 8%;
  }
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.bd-placeholder-img {
    font-size: 1.125rem;
    text-anchor: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  .auth{
    width: 30%;
    padding: 2%;
    margin: 2% auto;
    margin-top: 4%;
    background-color: #fff;
    border: 1px #ddd solid;
    text-align: center;
  }

  @media screen and (max-width: 1080px)
  {
    .auth{
      width: 35%;
    }
  }

  @media screen and (max-width: 720px)
  {
    .auth{
      width: 55%;
    }
  }

  @media screen and (max-width: 640px)
  {
    .auth{
      width: 60%;
    }
  }

  @media screen and (max-width: 480px)
  {
    .auth{
      width: 80%;
    }
  }

  .error_msg{
    color: red;
  }

  .hiw{
    color: #ffffff;
    padding: 10%;
    padding-top: 6%;
    font-size: 1.2em;
  }

  .succint-hiw{
    width: 15%;
    display: none;
    padding-right: 1%;
    color: #ffffff;
    font-size: 1.2em;
  }